import { useContext} from "react"
import { Context } from "../../Context/index";
import './About.css'
import { ImageList } from "./ImageList"
import { Carousel } from 'antd';

export const About = () => {
    const { listOfImage } = useContext(Context)

    const contentStyle = {
        height: '160px',
        color: '#fff',
        lineHeight: '160px',
        textAlign: 'center',
        background: '#364d79',
    };

    return (
        <div className="main__about">
            <div className="main__about__wrapper">
                <div className="container">
                    <div className="main__about__history">
                        <h1> </h1>
                        <p>   ,             
                             .         -, 
                                   ,   NaTrue, BDIH  ECOCERT.
                        </p>
                        <ul>      ?
                            <li>  - arolina    ,       
                                ,       .
                            </li>
                            <li>     .</li>
                            <li> 10 %    50$.</li>
                            <li>    .</li>
                            <li>  .      7.00  22.00  ,    
                                      .</li>
                        </ul>
                    </div>
                    <div className="slider">
                        <div className="slider__wrapper">
                            <ul className="main__about__product__img__list">
                                <Carousel autoplay>
                                {listOfImage.slice(0,10).map((image, index)=>{
                                    return <ImageList image={image} key={index} style={contentStyle}/>
                                })} 
                                </Carousel>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

